<template>
    <div>
        <el-row id="userInfoWrap">
            <el-col :span="18" :offset="3">
                <div class="grid-content bg-purple-dark">
                    <el-form :model="userInfo" ref="userInfo" :label-position="labelPosition" label-width="100px" class="demo-ruleForm">
                        <el-form-item label="" prop="name">
                            <el-input v-model="userInfo.name" placeholder="" readonly></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="gender">
                            <el-select v-model="userInfo.gender" placeholder="" disabled>
                                <el-option label="" value="1"></el-option>
                                <el-option label="" value="2"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="" prop="phone">
                            <el-input v-model="userInfo.phone" placeholder="" readonly></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="email">
                            <el-input v-model="userInfo.email" placeholder="" readonly>
                            </el-input>
                        </el-form-item>
                        <el-form-item label="" prop="email">
                            <el-input v-model="userInfo.email_verify === '1' ? '' : ''" placeholder="" readonly>
                                <el-button slot="append" v-show="userInfo.email_verify === '0'" @click="sendValidateEmail" :disabled="codeButton.disabled">{{codeButton.name}}</el-button>
                            </el-input>
                        </el-form-item>
                        <el-form-item label="" prop="created_at">
                            <el-input v-model="userInfo.created_at" placeholder="" readonly></el-input>
                        </el-form-item>
                        <el-form-item label="IP" prop="ip">
                            <el-input v-model="userInfo.ip" placeholder="IP" readonly></el-input>
                        </el-form-item>
                        <el-form-item label="IP" prop="ip_address">
                            <el-input v-model="userInfo.ip_address" placeholder="IP" readonly></el-input>
                        </el-form-item>
                    </el-form>
                </div>
            </el-col>
        </el-row>

        <el-row id="logoutWrap">
            <el-button type="danger" id="logoutBtn" @click="logout"></el-button>
        </el-row>
    </div>
</template>


<script>
    export default {
        data() {
            return {
                labelPosition: 'left',
                codeButton: {
                    name: '',
                    disabled: false,
                    time: 180
                },
                userInfo: {
                    name: '',
                    gender: '1',
                    phone: '',
                    email: '',
                    email_verify: '',
                    created_at: '',
                    ip: '',
                    ip_address: ''
                }
            };
        },
        methods: {
            logout(){
                let _this = this;
                this.$get('/account/logout')
                    .then(response => {
                        localStorage.removeItem('token');
                        _this.$message({
                            message: response.message,
                            type: 'success'
                        })
                        _this.$router.push('/login');
                    });
            },
            sendValidateEmail(){
                let _this = this;
                this.$get('/account/send-validate-email')
                    .then(response => {
                        this.$message({
                            message: response.message,
                            type: 'success',
                        });
                        this.codeButton.disabled = true;
                        let _this = this;
                        let interval = window.setInterval(function() {
                            _this.codeButton.name = '' + _this.codeButton.time + '';
                            --_this.codeButton.time;
                            if(_this.codeButton.time < 0) {
                                _this.codeButton.name = "";
                                _this.codeButton.time = 180;
                                _this.codeButton.disabled = false;
                                window.clearInterval(interval);
                            }
                        }, 1000);

                    });
            }
        },
        created: function () {
            let _this = this;
            this.$get('/account/info')
                .then(response => {
                    _this.userInfo = response.user;
                });
        }
    }
</script>
<style scoped>
    #userInfoWrap{
        margin-top: 30px;
    }
    #logoutWrap{
        text-align: center;
    }
    #logoutBtn{
        width: 80%;
    }
</style>