<template>
    <el-row id="registerWrap">
        <el-col :span="18" :offset="3">
            <div class="grid-content bg-purple-dark">
                <el-form :model="registerForm" :rules="rules" ref="registerForm" :label-position="labelPosition" label-width="100px" class="demo-ruleForm">
                    <el-form-item prop="name">
                        <el-input v-model="registerForm.name" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item prop="gender">
                        <el-select v-model="registerForm.gender" placeholder="">
                            <el-option label="" value="1"></el-option>
                            <el-option label="" value="2"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item prop="phone">
                        <el-input v-model="registerForm.phone" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item prop="code">
                        <el-input v-model="registerForm.code" placeholder="">
                            <el-button slot="append" @click="sendCode" :disabled="codeButton.disabled">{{codeButton.name}}</el-button>
                        </el-input>
                    </el-form-item>
                    <el-form-item prop="email">
                        <el-input v-model="registerForm.email" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item prop="password">
                        <el-input v-model="registerForm.password" placeholder="" type="password"></el-input>
                    </el-form-item>
                    <el-form-item prop="confirmPassword">
                        <el-input v-model="registerForm.confirmPassword" placeholder="" type="password"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="register('registerForm')"></el-button>
                        <el-button type="default" @click="login()"></el-button>
                    </el-form-item>
                </el-form>
            </div>
        </el-col>
    </el-row>
</template>


<script>
    export default {
        data() {
            var validatePhone = (rule, value, callback) => {
                var reg=/^1[3456789]\d{9}$/;
                if (value === '') {
                    callback(new Error(''));
                } else if(!reg.test(value)){
                    callback(new Error(''));
                }else{
                    callback();
                }
            };
            var validateEmail = (rule, value, callback) => {
                var reg=/^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
                if (value === '') {
                    callback(new Error(''));
                }else if(!reg.test(value)){
                    callback(new Error(''));
                }else{
                    callback();
                }
            };
            var validatePassword = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                }else{
                    if(this.registerForm.confirmPassword !== ''){
                        this.$refs.registerForm.validateField('confirmPassword');
                    }
                    callback();
                }
            };
            var validateConfirmPassword = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else if (value !== this.registerForm.password) {
                    callback(new Error('!'));
                }else{
                    callback();
                }
            };
            return {
                labelPosition: 'top',
                codeButton: {
                    name: '',
                    disabled: false,
                    time: 60
                },
                registerForm: {
                    name: '',
                    gender: '1',
                    phone: '',
                    code: '',
                    email: '',
                    password: '',
                    confirmPassword: ''
                },
                rules: {
                    phone: [
                        { required: true, message: '', trigger: 'blur' },
                        { validator: validatePhone }
                    ],
                    code: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    email: [
                        { required: true, message: '', trigger: 'blur' },
                        { validator: validateEmail }
                    ],
                    password: [
                        { required: true, message: '', trigger: 'change' },
                        { min:6, max:20, message: '6-20'},
                        { validator: validatePassword }
                    ],
                    confirmPassword: [
                        { required: true, message: '', trigger: 'change' },
                        { min:6, max:20, message: '6-20'},
                        { validator: validateConfirmPassword }
                    ]
                }
            };
        },
        methods: {
            sendCode() {
                this.$refs.registerForm.validateField('phone', (prop) => {
                    if (!prop) {
                        this.$post('/auth/register-code', {phone: this.registerForm.phone})
                            .then(response => {
                                this.$message({
                                    message: response.message,
                                    type: 'success',
                                });
                                this.codeButton.disabled = true;
                                let _this = this;
                                let interval = window.setInterval(function() {
                                    _this.codeButton.name = '' + _this.codeButton.time + '';
                                    --_this.codeButton.time;
                                    if(_this.codeButton.time < 0) {
                                        _this.codeButton.name = "";
                                        _this.codeButton.time = 60;
                                        _this.codeButton.disabled = false;
                                        window.clearInterval(interval);
                                    }
                                }, 1000);
                            });
                    } else {
                        return false;
                    }
                });
            },
            register(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.$post('/auth/register', this.registerForm)
                            .then(response => {
                                if(response.code == 1){
                                    localStorage.setItem('token', response.token);
                                    let _this = this;
                                    this.$message({
                                        message: response.message,
                                        type: 'success',
                                    });
                                    _this.$router.push('/');
                                }else{
                                    this.$message({
                                        message: response.message,
                                        type: 'error',
                                    });
                                }
                            });
                    } else {
                        return false;
                    }
                });
            },
            login() {
                this.$router.push('/login');
            }
        }
    }
</script>
<style scoped>
 #registerWrap{
     margin-top: 60px;
     text-align: left;
 }
</style>